import React, { useState } from 'react';



function CreateNote(props){
    const[expand,setExpand] = useState(false);

    const [note,setNote] = useState({

        title:"",
        content:"",
    });
    const expandIt = ()=>{
        setExpand(true);
    }

    const shrinkIt = ()=>{
        setExpand(false);
    }

    const InputEvent = (event)=>{
        const {name,value} = event.target;

        setNote((prevData)=>{
            return {
                ...prevData,
                [name]:value,
            };
        });
    };

    const addEvent = (event)=>{
        props.passNote(note);
        event.preventDefault();
        setNote({
            title:"",
            content:"",
        })

    }

    return(
        <>

        <div className="note-class" onDoubleClick = {shrinkIt}>
            <div className = "sub-note-class">

                <form>
                    {expand?
                    <input type = "text"
                     className="no-outline" placeholder="title"  
                     value={note.title} name = "title" onChange = {InputEvent} autoComplete="off"
                    /> : null} <br></br> 
                   
                    <textarea placeholder = "Write a note..." className="no-outline"
                    value = {note.content} name = "content" onChange = {InputEvent}
                    onClick={expandIt} 
                    />
                    {expand?
                    <button className="note-btn no-outline" onClick={addEvent}><i class="fa fa-plus" aria-hidden="true"></i>
                    </button>:null}
                </form>

            </div>
        </div>
   

        </>
    );
}

export default CreateNote;