import React from 'react';



function Note(props){

  const deleteNote = ()=>{
      props.deleteItem(props.id);

  }

    return(
        <>

       

        <div className="note">
      <h1>{props.title}</h1>
      <p>{props.content}</p>
      <button className="note-btn-del" onClick={deleteNote}><i class="fa fa-trash" aria-hidden="true"></i>
</button>
     
     
    </div>
        

        </>
    );
}

export default Note;